.sliderContainer {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  padding: 30px;
}

.slickSlide {
  display: flex !important;
  justify-content: center;
  align-items: center;
}

.slickSlide img {
  width: 100%;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  padding-top: 50px;
}
